<template>
    <div id="app">
        <v-header></v-header>
        <v-rightSider></v-rightSider>
        <div id="content" class="content">
            <v-courseHeader :courseData="courseData" :bus="bus"></v-courseHeader>
            <mu-row gutter>
                <mu-col width="100" tablet="70" desktop="70" class="tab-detail">
                    <mu-row>
                        <mu-col width="100" tablet="50" desktop="50">
                            <div class="tab">
                                <mu-tabs :value="activeTab" @change="handleTabChange">
                                    <mu-tab value="introduction" title="" />
                                    <mu-tab value="chapter" title="" />
                                    <mu-tab value="discussion" title="" />
                                </mu-tabs>
                            </div>
                        </mu-col>
                    </mu-row>
                    <div>
                        <!--  -->
                        <p class="introduction tab-page" :class="{active: activeTab === 'introduction'}">{{courseData.description}}</p>
                        <!--  -->
                        <div class="chapter tab-page" :class="{active: activeTab === 'chapter'}">
                            <mu-list>
                                <mu-list-item v-for="(section, sectionIndex) in chapter.sections" :title="''+(sectionIndex+1)+''+section.name" :key="section.id" toggleNested>
                                    <mu-list-item v-for="(chapter,chapterIndex) in section.chapter" :title="''+(chapterIndex+1)+''+chapter.name" :key="chapter.id" slot="nested"></mu-list-item>
                                </mu-list-item>
                            </mu-list>
                        </div>
                        <!--  -->
                        <div class="chapter tab-page" :class="{active: activeTab === 'discussion'}">
                            <mu-sub-header></mu-sub-header>
                            <p></p>
                            <mu-raised-button label="" class="demo-raised-button" secondary/>
                            <mu-sub-header></mu-sub-header>
                            <mu-list>
                                <mu-list-item class="sub-list-title"></mu-list-item>
                                <mu-list-item class="sub-list-title"></mu-list-item>
                                <mu-list-item class="sub-list-title"></mu-list-item>
                            </mu-list>
                            <mu-sub-header></mu-sub-header>
                            <mu-list>
                                <mu-list-item title="" :open="close" toggleNested disabled>
                                    <mu-avatar slot="left" src="/images/uicon.jpg" />
                                    <span slot="describe">
                                        <span>610</span>
                                    <span style="float:right">
                                             1   
                                            0  
                                            0
                                        </span>
                                    </span>
                                    <p></p>
                                    <mu-list-item slot="nested"></mu-list-item>
                                </mu-list-item>
                            </mu-list>
                        </div>
                    </div>
                </mu-col>
                <mu-col width="100" tablet="30" desktop="30">
                    <div class="comment-list">
                        <mu-list>
                            <mu-sub-header></mu-sub-header>
                            <mu-list-item :title="comment.name" v-for="comment in courseData.comments" :key="comment.id">
                                <mu-avatar :src="comment.post" slot="leftAvatar" />
                                <mu-icon value="grade" v-for="item in comment.star" :key="item" />
                                <mu-list-item slot="right" class="time">
                                    {{comment.time}}
                                </mu-list-item>
                            </mu-list-item>
                        </mu-list>
                    </div>
                    <div class="learner-list">
                        <mu-list>
                            <mu-sub-header>{{student_num}}</mu-sub-header>
                            <mu-list-item class="learner-avatar" v-for="student in courseData.students" :key="student.id">
                                <mu-avatar slot="left" :src="student.post" />
                            </mu-list-item>
                        </mu-list>
                    </div>
                </mu-col>
            </mu-row>
            <mu-snackbar v-if="snackbar" :message="alertText" @actionClick="hideSnackbar" @close="hideSnackbar" style="text-align:center"/>
        </div>
        <v-footer></v-footer>
    </div>
</template>
<script>
import header from 'components/common/header/header.vue'
import footer from 'components/common/footer/footer.vue'
import rightSider from 'components/common/rightSider/rightSider.vue'

import courseHeader from 'entry/courseDetail/courseHeader.vue'


const ERR_OK = 0
import Vue from 'vue'
export default {
    data() {
            return {
                courseData: {},
                activeTab: 'discussion',
                chapter: {},
                close: false,
                bus: new Vue(),
                snackbar: false,
                alertText:''
            }
        },
        components: {
            'v-header': header,
            'v-footer': footer,
            'v-rightSider': rightSider,
            'v-courseHeader': courseHeader
        },
        created() {
            console.log(this.cid)
            this.$http.get('/course/' + this.cid).then((response) => {
                    response = response.body
                        // console.log(response)
                    if (response.failure.length === 0) {
                        this.courseData = response.success
                    } else {
                        alert(response.failure[0])
                    }

                }),
                this.$http.get('/course/' + this.cid + '/section').then((response) => {
                    response = response.body
                        // console.log(response)
                    if (response.failure.length === 0) {
                        this.chapter = response.success
                        console.log(this.chapter)
                    } else {
                        alert(response.failure[0])
                    }

                });
                if (this.bus) {
                    const _self = this;
                    this.bus.$on("alertFlag", function(msg) {
                        console.log("");
                        console.log(msg)
                        _self.snackbar = msg.flag;
                        _self.alertText = msg.text;
                        _self.showSnackbar();
                    });
                }
        },
        methods: {
            handleUrl() {
                var url = location.search; //url"?" 
                var theRequest = new Object();
                if (url.indexOf("?") != -1) {
                    var str = url.substr(1);
                    var strs = str.split("&");
                    for (var i = 0; i < strs.length; i++) {
                        theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);
                    }
                }
                return theRequest;
            },
            handleTabChange(val) {
                this.activeTab = val
            },
            handleActive() {
                window.alert('tab active')
            },
            showSnackbar() {
                this.snackbar = true
                if (this.snackTimer) clearTimeout(this.snackTimer)
                this.snackTimer = setTimeout(() => {
                    this.snackbar = false
                }, 1000)
            },
            hideSnackbar() {
                this.snackbar = false
                if (this.snackTimer) clearTimeout(this.snackTimer)
            }
        },
        computed: {
            cid: function() {
                if (typeof this.handleUrl()['id'] == 'undefined') {
                    return 0;
                } else {
                    return this.handleUrl()['id']
                }

            },
            student_num() {
                try {
                    return this.courseData.students.length
                } catch (e) {
                    console.log('')

                }
            }
        }
}
</script>
<style lang="less">
@main-color: #009688;
.content {
    padding: 50px 100px;
    margin-top: 50px;
    .tab {
        position: relative;
        z-index: 0
    }
    .tab-detail {
        border: 1px solid #ccc;
        padding: 15px;
        // height: 500px;
    }
}

.comment-list {
    border: 1px solid #ccc;
    .time {
        .mu-item {
            display: inline-block;
            padding-right: 80px;
            font-size: 12px;
            color: #ccc;
        }
    }
    .mu-icon {
        font-size: 16px;
        color: #ffc107
    }
}

.learner-list {
    margin-top: 15px;
    border: 1px solid #ccc;
    .learner-avatar {
        float: left;
    }
}

.tab-page {
    display: none;
}

.tab-page.active {
    display: block;
}

.sub-list-title {
    border-bottom: 1px solid #ccc
}
</style>



